* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --dark-green: #09451c;
  --light-green: #96a68d;
  --bone: #e3e1dc;
  --orange: #ff8427;
  --blue: #2e4057;
  --burnt: #bb4430;
}
/* UTILITY CLASSES >>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>> */
/* HEIGHT / WIDTH UTILS */
.min-100-vh {
	min-height: 100vh;
}
.min-100vw {
	min-width: 100vw;
}

/* FONT UTILS */
.text-uppercase {
	text-transform: uppercase;
}

/* FLEX CONTENT */
.flex-row {
	display: flex;
	flex-wrap: wrap;
}
.flex-column {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
}
.justify-space-between {
	justify-content: space-between;
}
.justify-space-around {
	justify-content: space-around;
}
.justify-flex-end {
	justify-content: flex-end;
}

.justify-center {
	justify-content: center;
}

.align-stretch {
	align-items: stretch;
}

.align-end {
	align-items: flex-end;
}

.align-center {
	align-items: center;
}

