/* >>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>> */
header {
  background-size: cover;
  background-color: var(--dark-green);
  color: var(--bone);
  position: -webkit-sticky;
  top: 0;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

header img, .exit {
  width: 30px;
  height: 30px;
  color: var(--bone);
  filter: opacity(0.7) drop-shadow(0 0 0 var(--bone));
}

nav {
    display: none;
    position: absolute;
    left: 25%;
    top: 0;
    height: 100vh;
    width: 75vw;
    z-index: 2;
}

.menu-open {
    display: block;
}

.page-cover {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.6);
}

.menu {
    background-color: var(--bone);
    height: 100%;
    border: 1px solid var(--dark-green);
    color: var(--dark-green);
}

.menu-header {
    background-color: var(--dark-green);
    padding: 10px;
}

.exit {
    width: 10px;
    height: 10px;
}

nav ul {
    list-style: none;
    margin: 30px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.nav-link {
    font-size: 20px;
    color: var(--dark-green);
    text-decoration: none;
}

footer {
  background-size: cover;
  background-color: var(--dark-green);
  color: var(--bone);
  padding: 10px;
}

body {
  background-color: var(--bone);
  color: var(--dark-green);
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.5;
}

main {
  flex: 1;
}

.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.mountain-bg {
  background-image: url("./assets/images/mountains.jpg");
  background-size: cover;
  background-position: center 30%;
  color: var(--bone);
}

.centered {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

section {
  padding: 30px 45px;
}

form {
  display: flex;
  flex-direction: column;
}

.btn-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

input {
  border: 1px solid var(--dark-green);
  border-radius: 10px;
  padding: 10px;
}

button {
  border: none;
  border-radius: 5px;
  padding: 5px;
  margin-top: 15px;
  width: 70px;
  background-color: var(--dark-green);
  color: var(--bone);
  transition: background-color 0.15s, color 0.15s;
}

button:hover {
  background-color: var(--light-green);
  color: var(--bone);
}
